.clickText {
	position: absolute;
	top: 20%;
	left: 50%;
	transform: translate(-50%, -50%);
	font-size: larger;
	text-align: center;
}

.startButton {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background-image: url(./img/microphone.png);
	background-color: red;
	background-repeat: no-repeat;
	background-position: 50% 50%;
	height: 100px;
	width: 100px;
	border: none;
	border-radius: 50%;
	background-size: cover;
}

.startButton span {
	display: none;
}

.stopButton {
	position: absolute;
	top: 60%;
	left: 50%;
	transform: translate(-50%, -50%);
	background-color: red;
	background-repeat: no-repeat;
	background-position: 50% 50%;
	height: 50px;
	width: 50px;
	border: none;
}

.submitButton {
	position: absolute;
	top: 90%;
	left: 50%;
	transform: translate(-50%, -50%);
	border: none;
	background: #2b2d2f;
	height: 50px;
	width: 150px;
	text-align: center;
	cursor: pointer;
	border-radius: 4px;
	font: bold 1.25rem/1 poppins;
	color: #71dfbe;
}

.stopButton span {
	display: none;
}

.recordingTime {
	font-size: xx-large;
	text-align: center;
	position: absolute;
	top: 40%;
	left: 50%;
	transform: translate(-50%, -50%);
}

.recordingText {
	color: red;
}

.stopwatch {
	font-size: x-large;
}

.audioControls {
	position: absolute;
	top: 80%;
	left: 50%;
	transform: translate(-50%, -50%);
}
